@import "_normalize";
@import "_variables";
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600&display=swap&subset=latin-ext");
@import "modules/hero.scss";
@import "modules/skills.scss";
@import "modules/projects.scss";
@import "modules/contact.scss";

html {
	box-sizing: border-box;
	font-size: 16px;
	scroll-behavior: smooth;
	@media (max-width: $mobile) {
		font-size: 12px;
	}
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	color: $big-stone;
	background: #fff;
	font-family: "Poppins", sans-serif;
	border: 16px solid $big-stone;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	@media (max-width: $mobile) {
		border: none;
	}
}

.container {
	max-width: 1216px;
	padding: 0 16px;
	margin: 0 auto;
}

.hero__image {
	border-radius: 50%;
	width: 130px;
	height: 130px;
	border-color: $gray;
	border-width: 0.3rem;
	border-style: solid;
	padding: 4px;
}
